.contact-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 60vh;
  margin-top: 150px;
  padding: 50px;
}

.contact-container h1 {
  margin: 0 auto;
  font-size: 25px;
  border-bottom: 2px solid var(--color-sec);
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 600;
  color: var(--color-sec);
  opacity: 0.8;
  padding-bottom: 5px;
}

.contact-container .contact-links {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 40px;
}

.contact-container .contact {
  position: inherit;
  height: 280px;
  width: 30%;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition:  0.3s ease, transform 0.2s ease;
}

.contact-container .contact:hover {
  background-color: rgba(255, 255, 255, 0.096);
  transform: scale(1.1);
}

.contact-container .contact .icon {
  position: relative;
  font-size: 130px;
}

.contact-container .contact h2 {
  position: relative;
  margin-top: 15px;
  font-size: 21px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  text-transform: capitalize;
  font-family: 'Open Sans';
  text-align: center;
}

.contact-container .contact h2 span {
  font-size: 13px;
  font-weight: 100;
  margin-top: 3px;
  color: rgb(202, 202, 202);
}

@media (max-width: 1260px) {
  .contact-container {
    margin-top: 20px;
  }
}

@media (max-width: 820px) {

  .contact-container h1 {
    font-size: 22px;
  }

  .contact-container .contact-links {
    flex-direction: column;
    justify-content: space-around;
  }

  .contact-container .contact {
    width: 80%;
    margin: 25px;
    backdrop-filter: blur(20px);
  }
}

@media (max-width: 500px) {
  
  .contact-container .contact {
    width: 95%;
  }

  .contact-container .contact h2 span {
    font-size: 13px;
  }
}

@media (max-width: 420px) {
  .contact-container {
    min-height: 80vh;
    margin-top: 70px;
    padding: 30px;
  }

  .contact-container h1 {
    font-size: 25px;
  }

  .contact-container .contact-links {
    margin-top: 30px;
  }

  .contact-container .contact .icon {
    font-size: 150px;
  }

  .contact-container .contact h2 {
    font-size: 25px;    
  }

  .contact-container .contact h2 span {
    font-size: 15px;
  }
}

@media (max-width: 375px) {
  .contact-container {
    margin-top: 100px;
    padding: 15px;
  }

  .contact-container .contact-links {
    margin-top: 20px;
  }

  .contact-container .contact {
    height: 290px;
  }

}
